#productos {
	float: none;
	article{
		width: 80%;
		margin: 0 auto;
		h2 {
			padding: 40px 0;
			letter-spacing: 3px;
		}
		.contain-products{
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			div.product {
				margin: 20px 0;
				font-family: lato;
				width: 30%;
				display: inline-block;
				padding: 0 5px;
				background: #fff;
				img {
					width: 100%;
				}
				.info-product {
    				width: 100%;
 					text-align: center;
    				padding: 10px 0;
    				letter-spacing: 4px;
    				line-height: 23px;
    				.buttons {
    					color: @azul;
    					cursor: pointer;
    				}
				}
			}
		}
	}
}